<template>
  <div class="p-1 pt-4">
    <b-form @submit.prevent="submit">
      <!-- <b-row>
        <b-col cols="12" xl="5" class="ml-1">
          <label id="Label" class="Label"
            >{{ tableyear }}{{ $t("IndustryType") }}{{ companysizeName }}
          </label>
        </b-col>
      </b-row> -->
      <b-row>
        <b-col cols="12" class="ml-3">
          <b-button
            variant="primary"
            class="px-4 required-btnAny align-content-center"
            >{{ $t("REQUIREDBUTTON") }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel">{{ $t("Industry") }}</label>
        </b-col>
        <b-col cols="11" xl="4" class="ml-2">
          <b-form-select
            v-model="companySettingInfo.selectedIndustry"
            :plain="true"
            :options="industryOptions"
            value-field="value"
            text-field="text"
            disabled-field="disabled"
            class="optionClass"
          ></b-form-select>
          <show-errors
            :control="$v.companySettingInfo.selectedIndustry"
            :field="$t('Industry')"
          />
        </b-col>
        <b-col cols="12" xl="4" class="file-btn-margin mobilejustify">
          <b-button
            :disabled="isRowSelected"
            @click="createCompanyInfo"
            variant="primary"
            >{{ $t("ADD") }}
            <i v-if="addBusy" class="fa fa-spinner fa-spin" />
          </b-button>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-2">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("YEAR1") }}</label>
          </b-col>
          <b-col id="options" cols="12" class="mt-3">
            <b-form-select
              v-model="selectedYear"
              :plain="true"
              :options="options"
              :disabled="onRowDisabled"
              value-field="item"
              text-field="name"
              class="optionClass"
              disabled-field="notEnabled"
            ></b-form-select>
          </b-col>
        </b-col>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-2">
            <b-button variant="primary" class="px-4 required-btnAny">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("EmployeeNumber")
            }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model.trim="companySettingInfo.employee"
              :placeholder="'1,000'"              
              maxlength="7"
              class="textFieldClass"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.companySettingInfo.employee"
              :field="$t('EmployeeNumber')"
            />
          </b-col>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-2">
            <b-button variant="primary" class="px-4 required-btnAny">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel w-80">{{ $t("Capital") }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model.trim="companySettingInfo.capital"
              :placeholder="'5,000,000' + $t('YEN')"              
              maxlength="14"
              class="textFieldClass"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.companySettingInfo.capital"
              :field="$t('Capital')"
            />
          </b-col>
        </b-col>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-2">
            <b-button variant="primary" class="px-4 required-btnAny">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("Revenue") }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model.trim="companySettingInfo.sales"
              :placeholder="'10,000,000' + $t('YEN')"              
              maxlength="14"
              class="textFieldClass"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.companySettingInfo.sales"
              :field="$t('Revenue')"
            />
          </b-col>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12" xl="6">
          <b-col cols="12">
            <b-button variant="primary" class="px-4 required-btnAny">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("FloorSpace") }}</label>
          </b-col>
          <b-col cols="12">
            <b-form-input
              v-model.trim="companySettingInfo.floorSize"
              :placeholder="'100' + $t('PING')"              
              maxlength="9"
              class="textFieldClass"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.companySettingInfo.floorSize"
              :field="$t('FloorSpace')"
            />
          </b-col>
        </b-col>
        <b-col cols="12" xl="6">
          <b-col cols="12">
            <b-button variant="outline-primary" class="px-4 required-btnAny">{{
              $t("REQUIREDBUTTON")
            }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("NumberOfStores")
            }}</label>
          </b-col>
          <b-col cols="12">
            <b-form-input
              v-model.trim="companySettingInfo.storeNo"
              :placeholder="'50'"              
              maxlength="7"
              class="textFieldClass"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.companySettingInfo.storeNo"
              :field="$t('NumberOfStores')"
            />
          </b-col>
        </b-col>
      </b-row>
      <br />
      <b-row class="mt-1">
        <b-col cols="6" class="text-right">
          <b-button
            :disabled="isAddSelected"
            @click="updateCompanyInfo"
            variant="primary"
            >{{ $t("UPDATE") }}
            <i v-if="updBusy" class="fa fa-spinner fa-spin" />
          </b-button>
        </b-col>
        <b-col cols="6" class="text-left">
          <b-button
            @click="clearbasicSettingInfo"
            variant="primary"
            class="btnWidth disableBtn"
            >{{ $t("CLEAR") }}</b-button
          >
        </b-col>
      </b-row>
      <br />
      <b-card no-body>
        <div id="settingTable">
          <b-table
            id="selectableTable"
            ref="selectableTable"
            :busy="isBusy"
            :select-mode="selectModeTable"
            :fields="fieldss"
            :items="companyData"
            :striped="true"
            :bordered="false"
            :borderless="false"
            head-variant="dark"
            fixed
            sticky-header
          >
            <template v-slot:cell(selectRow)="item">
              <template>
                <b-form-radio
                  :id="'radio-' + item.index"
                  v-model="item.item.selectRow"
                  :name="'table-radio'"
                  @change="toggleSelectAll(item)"
                ></b-form-radio>
              </template>
            </template>
            <template v-slot:cell(Year)="item">{{ item.value }}</template>
            <template v-slot:cell(EmployeeNumber)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(Capital)="item">{{ item.value }}</template>
            <template v-slot:cell(Revenue1)="item">{{ item.value }}</template>
            <template v-slot:cell(Floorspace)="item">{{ item.value }}</template>
            <template v-slot:cell(NumberOfStores)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(IndustryType)="item">{{
              item.value
            }}</template>
            <template v-slot:table-busy>
              <div class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
              </div>
            </template>
          </b-table>
          <div class="text-center text-danger mb-2 mt-2">
            {{ errorMessage }}
          </div>
        </div>
      </b-card>
    </b-form>
    <div>
      <b-modal
        id="tree-modal1"
        ref="tree-modal1"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="md"
        footer-class="border-0"
        header-class="border-0 bg-white pd-0"
        button-size="sm"
        hide-header-close
      >
        <template v-slot:modal-header="{}">
          <div class="w-100 d-flex justify-content-between">
            <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
              {{ $t("INDUSTRYSELECTION") }}
            </div>
            <b-button
              @click="cancel"
              class="back-btn close-btn mr-3 mt-2"
              variant="primary"
              >{{ $t("CLOSE") }}</b-button
            >
          </div>
        </template>
        <b-card class="pop-up-box">
          <b-row>
            <b-col>
              <SubMenuTree
                :treeTitle="industrytitle"
                @update-expense-type="selectIndustryItem"
                :industry="true"
                :data="treeData"
                :tree-options="treeOptions"
              />
            </b-col>
          </b-row>
        </b-card>
        <b-row>
          <b-col cols="12" class="text-center mb-2">
            <b-button
              :disabled="currentSelectedIndustryItem == ''"
              @click="getIndustryName"
              class="float-right"
              variant="primary"
              >{{ $t("SELECTEXPENSEITEMS") }}</b-button
            >
          </b-col>
        </b-row>
      </b-modal>
    </div>
  </div>
</template>
<script>
import { Settings } from "luxon";
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import SubMenuTree from "@/components/SubMenuTree";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "Index",
  components: { SubMenuTree, ShowErrors },
  data() {
    return {
      industryOptions: [],
      industrytitle: "",
      onRowDisabled: false,
      treeOptions: {},
      tableyear: "",
      companysizeName: "",
      allTreeData: [],
      treeData: [],
      options: [],
      monthOptions: [
        { item: "1", name: "1月" },
        { item: "2", name: "2月" },
        { item: "3", name: "3月" },
        { item: "4", name: "4月" },
        { item: "5", name: "5月" },
        { item: "6", name: "6月" },
        { item: "7", name: "7月" },
        { item: "8", name: "8月" },
        { item: "9", name: "9月" },
        { item: "10", name: "10月" },
        { item: "11", name: "11月" },
        { item: "12", name: "12月" },
      ],
      selectedYear: new Date().getFullYear(),
      isAddSelected: true,
      isRowSelected: false,
      companyData: [],
      companySizeInfoData: [],
      selectModeTable: "single",
      selectedRow: [],
      currentMonth: new Date().getMonth() + 1,
      companySettingInfo: {
        selectedIndustry: "",
        Datayear: "",
        employee: "",
        capital: "",
        sales: "",
        floorSize: "",
        storeNo: "",
        industry: "",
      },
      ClientCompanyData: [],
      isBusy: false,
      addBusy: false,
      updBusy: false,
      errorMessage: "",
      currentSelectedIndustryItem: "",
    };
  },
  computed: {
    ...mapGetters({
      selected_industry_item: "getSelectedIndustryItem",
      client_details: "getClientData",
    }),
    fieldss() {
      return [
        {
          key: "selectRow",
          label: this.$t("SELECT"),
          class: "text-center normal-width",
        },
        {
          key: "Year",
          label: this.$t("YEAR1"),
          class: "text-center normal-width",
        },
        {
          key: "EmployeeNumber",
          label: this.$t("EmployeeNumber"),
          class: "text-center normal-width",
        },
        {
          key: "Capital",
          label: this.$t("Capital1"),
          class: "text-center normal-width",
        },
        {
          key: "Revenue",
          label: this.$t("Revenue1"),
          class: "text-center normal-width",
        },

        {
          key: "Floorspace",
          label: this.$t("FloorSpace2"),
          class: "text-center normal-width",
        },
        {
          key: "NumberOfStores",
          label: this.$t("NumberOfStores"),
          class: "text-center normal-width",
        },
        {
          key: "IndustryType",
          label: this.$t("Industry1"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  validations: {
    companySettingInfo: {
      capital: { required },
      employee: { required },
      sales: { required },
      floorSize: { required },
      storeNo: { required },
      selectedIndustry: { required },
    },
  },
  watch: {
    "companySettingInfo.capital"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedCapital(newVal);
    },
    "companySettingInfo.employee"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedEmployee(newVal);
    },
    "companySettingInfo.floorSize"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedFloorSize(newVal);
    },
    "companySettingInfo.storeNo"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedStoreNo(newVal);
    },
    "companySettingInfo.sales"(newVal) {
      //to work with changes in "myArray"
      this.commaSeparatedRevenue(newVal);
    },
  },
  mounted() {
    this.prefectureData();
    this.companyTableData();
    this.$store.dispatch("updateIndustryTree", this.treeData);
  },
  created() {},
  methods: {
    /**Function to compile data for selected row */
    toggleSelectAll(item) {
      this.$refs.selectableTable.selectRow(item.index);
      item.item.selectRow = !item.item.selectRow;
      let selectedItem = [item.item];
      this.onRowSelected(selectedItem);
      // this.refreshStore();
    },
    commaSeparatedCapital(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.capital = data ? data + this.$t("YEN") : "";
    },
    commaSeparatedEmployee(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.employee = data ? data  : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedFloorSize(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.floorSize = data ? data + this.$t("PING") : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedStoreNo(newVal) {
      console.log(newVal, "val");
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.storeNo = data ? data  : ""; //.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparatedRevenue(newVal) {
      let data = this.commaSeparater(newVal);
      this.companySettingInfo.sales = data ? data  : ""; //newVal.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    commaSeparater(val) {
      return val.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    /**
     * @description function to fetch all industry details
     * @name getIndustrytTreeData
     */
    async getIndustryTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allTreeData = responseData;
        if (this.selected_industry_item.industry_nm) {
          this.companySettingInfo.industry = this.selected_industry_item.industry_nm;
        } else {
          this.allTreeData.forEach((item) => {
            if (
              this.companyIndustryNo == item.industry_no &&
              item.industry_kbn == 2
            ) {
              this.companySettingInfo.industry = item.industry_nm;
            }
          });
        }

        let mapData = [];
        let industryData = {
          text: "業種を選ぶ",
          value: "",
          disabled: true,
        };
        this.industryOptions.push(industryData);
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let ParentData = [];
          groupedData[key].forEach((item) => {
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              ParentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };
              industryData = {
                text: item.industry_nm,
                value: item.industry_no,
              };
              this.industryOptions.push(industryData);
              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + ParentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });

        this.$store.dispatch("updateIndustryTree", mapData);
      }
    },
    /**
     * @description function to  select industry data
     * @name selectIndustryItem
     */
    selectIndustryItem(item) {
      this.currentSelectedIndustryItem = item;
    },
    /**
     * @description function to update selected industry name and no to the store
     * @name updateIndustryItem
     */
    updateIndustryItem(item) {
      let Kbn2Data = [];
      this.allTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          Kbn2Data.push(item1);
        }
      });
      let treeItem = this._.find(Kbn2Data, { industry_nm: item });
      this.$store.dispatch("updateselectedIndustryItem", {
        industry_nm: treeItem.industry_nm,
        industry_no: treeItem.industry_no,
      });
    },
    /**
     * @description function to display industry name in screen
     * @name getIndustryName
     */
    getIndustryName() {
      this.updateIndustryItem(this.currentSelectedIndustryItem);
      if (this.selected_industry_item.industry_nm) {
        this.companySettingInfo.industry = this.selected_industry_item.industry_nm;
      }

      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to hide the popup modal
     * @name cancel
     */
    cancel() {
      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to set year options
     * @name prefectureData
     */
    prefectureData() {
      let startyear = parseInt(new Date().getFullYear()) - 10;
      let endYear = parseInt(new Date().getFullYear()) + 10;
      for (let index = startyear; index <= endYear; index++) {
        this.options.push({ item: index, name: index });
      }
    },
    /**
     * @description function to fetch row data in tabel on select on row items
     * @name onRowSelected
     */
    onRowSelected(items) {
      if (items.length > 0) {
        this.onRowDisabled = true;
        this.isRowSelected = true;
        this.isAddSelected = false;
        let year = items[0].Year;
        let yeardata = parseInt(year);
        this.yearInfo = yeardata;
        this.selectedYear = yeardata;
        this.companySettingInfo.employee = items[0].EmployeeNumber;
        this.companySettingInfo.capital = items[0].Capital;
        if (items[0].Revenue == null) {
          this.companySettingInfo.sales = null;
        } else {
          this.companySettingInfo.sales = items[0].Revenue;
        }
        if (items[0].Floorspace == null) {
          this.companySettingInfo.floorSize = null;
        } else {
          this.companySettingInfo.floorSize = items[0].Floorspace;
        }
        this.companySettingInfo.storeNo = items[0].NumberOfStores;
        this.tableyear = year;
        if (items[0].IndustryType == this.$t("Big")) {
          //大、中、小
          this.companysizeName = this.$t("BigIndustry");
        } else if (items[0].IndustryType == this.$t("Medium")) {
          this.companysizeName = this.$t("MediumIndustry");
        } else if (items[0].IndustryType == this.$t("Small")) {
          this.companysizeName = this.$t("SmallIndustry");
        } else if (items[0].IndustryType == "") {
          this.companysizeName = "";
        }
        let industryNo;
        this.companySizeInfoData.forEach((item) => {
          if (item.info_year == yeardata) {
            industryNo = item.industry_no;
          }
        });
        let KbnFlag = false;
        if (industryNo != null) {
          this.companySettingInfo.selectedIndustry = industryNo;
          this.allTreeData.forEach((item) => {
            if (item.industry_no == industryNo && item.industry_kbn == 2) {
              this.companySettingInfo.industry = item.industry_nm;
              KbnFlag = true;
            }
          });
        } else {
          this.companySettingInfo.industry = "";
        }
        if (KbnFlag == false) {
          this.companySettingInfo.industry = "";
        }
      } else {
        this.onRowDisabled = false;
        this.clearbasicSettingInfo();
      }
    },
    /**
     * @description function to remmove charecters
     * @name removeChar
     */
    removeChar(value) {
      try {
        return value.substring(1).replace(/[^0-9.-]+/g, "");
      } catch (error) {
        return 0;
      }
    },
    /**
     * @description function to convert currency to number format
     * @name NumberConvert
     */
    NumberConvert(value) {
      try {
        return value.replace(/[^0-9.-]+/g, "");
      } catch (error) {
        console.log(error);
      }
    },
    /**
     * @description function to convert number to currency format
     * @name filterByCurrency
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch companySizeInfo Tabel Data
     * @name companyTabelData
     */
    async companyTableData() {
      this.isBusy = true;
      let companysizeInfo = [];

      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTCOMPANYSIZEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      this.isBusy = false;
      this.ClientCompanyData = data.data;
      if (this.ClientCompanyData.length > 0) {
        this.errorMessage = "";
        let responseData = data.data;
        this.companySizeInfoData = responseData;
        let sortedData = this._.sortBy(responseData, "info_year").reverse();
        sortedData.forEach((item) => {
          if (item.company_size == 1) {
            //大、中、小
            this.companytype = this.$t("Small");
          } else if (item.company_size == 2) {
            this.companytype = this.$t("Medium");
          } else if (item.company_size == 3) {
            this.companytype = this.$t("Big");
          } else {
            this.companytype = "";
          }
          let revenue;
          let FloorSpace;
          if (item.revenue == null && item.floor_space == null) {
            revenue = null;
            FloorSpace = null;
          } else if (item.revenue != null && item.floor_space == null) {
            revenue =this.filterByCurrency(item.revenue);
            FloorSpace = null;
          } else if (item.revenue == null && item.floor_space != null) {
            revenue = null;
            FloorSpace = FloorSpace = this.filterByCurrency(item.floor_space);
          } else {
            revenue = this.filterByCurrency(item.revenue);

            FloorSpace = this.filterByCurrency(item.floor_space);
          }

          let tableinfo = {
            Year: item.info_year,
            EmployeeNumber: item.employee_number + "人",
            Capital:this.filterByCurrency(item.capital),
            Revenue: revenue,
            Floorspace: FloorSpace,
            NumberOfStores: item.number_of_stores,
            IndustryType: this.companytype,
          };
          companysizeInfo.push(tableinfo);
        });

        this.companyData = companysizeInfo;
        this.tableyear = this.companyData[0].Year;
        if (this.companyData[0].IndustryType == this.$t("Big")) {
          //大、中、小
          this.companysizeName = this.$t("BigIndustry");
        } else if (this.companyData[0].IndustryType == this.$t("Medium")) {
          this.companysizeName = this.$t("MediumIndustry");
        } else if (this.companyData[0].IndustryType == this.$t("Small")) {
          this.companysizeName = this.$t("SmallIndustry");
        } else if (this.companyData[0].IndustryType == "") {
          this.companysizeName = "";
        }
        this.companyIndustryNo = sortedData[0].industry_no;
        this.getIndustryTreeData();
      } else {
        this.isBusy = false;
        this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
      }
    },
    /**
     * @description function to show popup modal
     * @name industryTree
     */
    industrtyTree() {
      this.currentSelectedIndustryItem = "";
      this.$refs["tree-modal1"].show();
    },
    /**
     * @description function to clear the data in screen
     * @name clearbasicSettingInfo
     */
    clearbasicSettingInfo() {
      this.$v.$reset();
      this.$refs.selectableTable.clearSelected();
      this.selectedYear = new Date().getFullYear();
      this.isRowSelected = false;
      this.isAddSelected = true;
      this.companySettingInfo.employee = "";
      this.companySettingInfo.capital = "";
      this.companySettingInfo.sales = "";
      this.companySettingInfo.floorSize = "";
      this.companySettingInfo.storeNo = "";
      this.companySettingInfo.selectedIndustry = "";
      this.onRowDisabled = false;
      this.companyTableData();
    },
    /**
     * @description function to check create confirmation
     * @name createCompanyInfo
     */
    async createCompanyInfo() {
      this.$v.companySettingInfo.$touch();
      if (!this.$v.companySettingInfo.$invalid) {
        this.addBusy = true;
        if (
          this.companySettingInfo.employee == "" ||
          parseInt(this.companySettingInfo.employee) == 0
        ) {
          this.addBusy = false;
          this.$swal({
            type: "warning",
            text: this.$t("WarningMessage1"),
          });
        } else if (
          this.companySettingInfo.capital == "" ||
          parseInt(this.companySettingInfo.capital) == 0
        ) {
          this.addBusy = false;
          this.$swal({
            type: "warning",
            text: this.$t("WarningMessage2"),
          });
        } else {
          this.isAddSelected = true;
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERREADDATAMCLIENTCOMPANYSIZEINFO,
            {
              fieldlist: ["*"],
              conditions: [
                {
                  key: "client_no",
                  value: localStorage.getItem("client_account_no"),
                },
              ],
            }
          );
          this.addBusy = false;
          let responseData = data.data;
          let Dataflag = true;
          responseData.forEach((item) => {
            if (
              item.client_no == localStorage.getItem("client_account_no") &&
              item.info_year == this.selectedYear
            ) {
              Dataflag = false;
            }
          });
          if (Dataflag == true) {
            let self = this;
            this.$swal({
              text: this.$t("ConformationMessage"),
              showCancelButton: true,
              confirmButtonText: "OK",
              cancelButtonText: this.$t("CANCEL"),
              cancelButtonColor: "#062854",
              cancelButtonClass: "btn-primary",
            }).then(async function(isconfirm) {
              if (!isconfirm.value == true) {
                return;
              } else {
                self.createInfo();
              }
            });
          } else {
            this.addBusy = false;
            this.$swal({
              type: "warning",
              text: this.$t("ErrorMessage"),
            });
          }
        }
      }
    },
    /**
     * @description function to crate new record in ceateInfo
     * @name createInfo
     */
    async createInfo() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATEMCLIENTCOMPANYSIZEINFO,
        [
          {
            client_no: localStorage.getItem("client_account_no"),
            info_year: this.selectedYear,
            company_size: "",
            industry_no: this.companySettingInfo.selectedIndustry,
            employee_number: parseInt(
                this.NumberConvert(
                  this.companySettingInfo.employee.slice(0, -1)
                )
            ),
            capital: parseInt(
                this.NumberConvert(this.companySettingInfo.capital.slice(0, -1))
            ),
            revenue: parseInt(
                this.NumberConvert(this.companySettingInfo.sales)
            ),
            floor_space: parseInt(
                this.NumberConvert(
                  this.companySettingInfo.floorSize.slice(0, -1)
                )
            ),
            number_of_stores: parseInt(
                this.NumberConvert(this.companySettingInfo.storeNo.slice(0, -2))
            ),       
            del_flg: false,
            create_id: localStorage.getItem("client_account_no"),
            create_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            update_id: localStorage.getItem("client_account_no"),
            update_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            created_at: null,
            updated_at: null,
          },
        ]
      );
      if (data.success) {
        this.companyTableData();
        this.clearbasicSettingInfo();
      }
    },
    /**
     * @description function to check update confirmation
     * @name updateCompanyInfo
     */
    async updateCompanyInfo() {
      this.$v.companySettingInfo.$touch();
      if (!this.$v.companySettingInfo.$invalid) {
        this.updBusy = true;
        if (
          this.companySettingInfo.employee == "" ||
          parseInt(this.companySettingInfo.employee) == 0
        ) {
          this.updBusy = false;
          this.$swal({
            type: "warning",
            text: this.$t("WarningMessage1"),
          });
        } else if (
          this.companySettingInfo.capital == "" ||
          parseInt(this.companySettingInfo.capital) == 0
        ) {
          this.updBusy = false;
          this.$swal({
            type: "warning",
            text: this.$t("WarningMessage2"),
          });
        } else {
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERREADMCLIENTCOMPANYSIZEINFO,
            {
              fieldlist: ["*"],
              conditions: [
                {
                  key: "client_no",
                  value: localStorage.getItem("client_account_no"),
                },
              ],
            }
          );
          this.updBusy = false;
          let responseData = data.data;
          let Dataflag = false;
          responseData.forEach((item) => {
            if (
              item.client_no == localStorage.getItem("client_account_no") &&
              item.info_year == this.yearInfo
            ) {
              this.client_info_no = item.company_size_info_no;
              this.industryNo = item.industry_no;
              this.companysize = item.company_size;
              this.createDate = item.create_dt;
              Dataflag = true;
            }
          });
          if (Dataflag == true) {
            let self = this;
            this.$swal({
              text: this.$t("UpdateMessage"),
              showCancelButton: true,
              confirmButtonText: "OK",
              cancelButtonText: this.$t("CANCEL"),
              cancelButtonColor: "#062854",
              cancelButtonClass: "btn-primary",
            }).then(async function(isconfirm) {
              if (!isconfirm.value == true) {
                this.updBusy = false;
                return;
              } else {
                self.updateInfo();
              }
            });
          } else {
            this.$swal({
              type: "warning",
              text: this.$t("ErrorMessage2"),
            });
          }
        }
      }
    },
    /**
     * @description function to update companySizeInfo tabel record
     * @name updateInfo
     */
    async updateInfo() {
      console.log(
        "this.companySettingInfo.capital",
        this.companySettingInfo.capital
      );
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATEMCLIENTCOMAPNYSIZEINFO,
        {
          data: [
            {
              client_no: localStorage.getItem("client_account_no"),
              info_year: this.selectedYear,
              company_size: this.companysize,
              industry_no: this.companySettingInfo.selectedIndustry,
              employee_number: parseInt(
                this.NumberConvert(
                  this.companySettingInfo.employee.slice(0, -1)
                )
              ),
              capital: parseInt(
                this.NumberConvert(this.companySettingInfo.capital.slice(0, -1))
              ),
              revenue: parseInt(
                this.NumberConvert(this.companySettingInfo.sales)
              ),
              floor_space: parseInt(
                this.NumberConvert(
                  this.companySettingInfo.floorSize.slice(0, -1)
                )
              ),
              number_of_stores: parseInt(
                this.NumberConvert(this.companySettingInfo.storeNo.slice(0, -2))
              ),
              del_flg: false,
              create_id: localStorage.getItem("client_account_no"),
              create_dt: this.createDate,
              update_id: localStorage.getItem("client_account_no"),
              update_dt:
                new Date().getFullYear() +
                "-" +
                this.currentMonth +
                "-" +
                new Date().getDate() +
                " " +
                new Date().getHours() +
                ":" +
                new Date().getMinutes() +
                ":" +
                new Date().getSeconds(),
              created_at: null,
              updated_at: null,
            },
          ],
          conditions: [
            { key: "company_size_info_no", value: this.client_info_no },
          ],
        }
      );
      if (data.success) {
        this.companyTableData();
        this.clearbasicSettingInfo();
      }
    },
  },
};
</script>
